import { provide, ref } from "vue";
import Magnify from "vue-material-design-icons/Magnify.vue";
import MessageOutline from "vue-material-design-icons/MessageOutline.vue";
import { t } from "@nextcloud/l10n";
import NcButton from "@nextcloud/vue/dist/Components/NcButton.js";
import NcDialog from "@nextcloud/vue/dist/Components/NcDialog.js";
import NcEmptyContent from "@nextcloud/vue/dist/Components/NcEmptyContent.js";
import NcTextField from "@nextcloud/vue/dist/Components/NcTextField.js";
import ConversationsSearchListVirtual from "./LeftSidebar/ConversationsList/ConversationsSearchListVirtual.vue";
import { CONVERSATION } from "../constants.js";
import { hasTalkFeature } from "../services/CapabilitiesManager.ts";
import { searchListedConversations, fetchConversations } from "../services/conversationsService.js";
export default {
  name: "RoomSelector",
  components: {
    ConversationsSearchListVirtual,
    NcButton,
    NcDialog,
    NcEmptyContent,
    NcTextField,
    // Icons
    Magnify,
    MessageOutline
  },
  props: {
    dialogTitle: {
      type: String,
      default: t("spreed", "Link to a conversation")
    },
    dialogSubtitle: {
      type: String,
      default: ""
    },
    /**
     * Whether to only show conversations to which the user can post messages.
     */
    showPostableOnly: {
      type: Boolean,
      default: false
    },
    /**
     * Whether to only show open conversations to which the user can join.
     */
    listOpenConversations: {
      type: Boolean,
      default: false
    },
    /**
     * Whether component is used as plugin and should emit on $root.
     */
    isPlugin: {
      type: Boolean,
      default: false
    }
  },
  emits: ["close", "select"],
  setup() {
    const selectedRoom = ref(null);
    provide("selectedRoom", selectedRoom);
    return {
      selectedRoom
    };
  },
  data() {
    return {
      rooms: [],
      currentRoom: null,
      searchText: "",
      loading: true
    };
  },
  computed: {
    availableRooms() {
      return this.rooms.filter(
        (room) => room.type !== CONVERSATION.TYPE.CHANGELOG && room.objectType !== CONVERSATION.OBJECT_TYPE.FILE && room.objectType !== CONVERSATION.OBJECT_TYPE.VIDEO_VERIFICATION && (!this.currentRoom || this.currentRoom !== room.token) && (!this.showPostableOnly || room.readOnly === CONVERSATION.STATE.READ_WRITE) && (!this.searchText || room.displayName.toLowerCase().includes(this.searchText.toLowerCase()))
      );
    },
    noMatchFoundTitle() {
      return this.listOpenConversations ? t("spreed", "No open conversations found") : t("spreed", "No conversations found");
    },
    noMatchFoundSubtitle() {
      return this.listOpenConversations ? t("spreed", "Either there are no open conversations or you joined all of them.") : t("spreed", "Check spelling or use complete words.");
    }
  },
  beforeMount() {
    this.fetchRooms();
    const $store = OCA.Talk?.instance?.$store;
    if ($store) {
      this.currentRoom = $store.getters.getToken();
    }
  },
  methods: {
    t,
    async fetchRooms() {
      const response = this.listOpenConversations ? await searchListedConversations({ searchText: "" }, {}) : await fetchConversations({});
      this.rooms = response.data.ocs.data.sort(this.sortConversations).filter((conversation) => !hasTalkFeature(this.currentRoom, "federation-v1") || !conversation.remoteServer);
      this.loading = false;
    },
    sortConversations(conversation1, conversation2) {
      if (conversation1.isFavorite !== conversation2.isFavorite) {
        return conversation1.isFavorite ? -1 : 1;
      }
      return conversation2.lastActivity - conversation1.lastActivity;
    },
    clearText() {
      this.searchText = "";
    },
    close() {
      if (this.isPlugin) {
        this.$root.$emit("close");
      } else {
        this.$emit("close");
      }
    },
    onSelect(item) {
      this.selectedRoom = item;
    },
    onSubmit() {
      if (this.isPlugin) {
        this.$root.$emit("select", this.selectedRoom);
      } else {
        this.$emit("select", this.selectedRoom);
      }
    }
  }
};
